.skills {
	padding: 78px 0 110px 0;
	background-color: #ffffff;
	&__title {
		font-family: Gilroy-Bold;
		font-size: 34px;
		line-height: calc(42 / 34);
		text-align: center;
		color: #070707;

		text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		margin: 0 auto;
	}
	&__text {
		width: 228px;
		font-family: Gilroy-Medium;
		font-size: 18px;
		line-height: 22px;
		text-align: center;
		color: #070707;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 70px;
		margin-top: 50px;
		@media screen and (min-width: 940px) {
			margin-bottom: 83px;
			margin-top: 69px;
		}
	}
}

.apps {
	&__list {
		list-style: none;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;

		width: calc(100% - 30px);

		&:not(:last-child) {
			margin-right: 0;
			margin-bottom: 80px;

			@media screen and (min-width: 940px) {
				margin-bottom: 0;
			}
		}

		@media screen and (min-width: 940px) {
			width: calc((100% - 45px) / 4);
		}
	}
	&__name {
		font-family: Gilroy-Bold;
		font-size: 14px;
		line-height: calc(17 / 14);
		text-align: center;
		margin-top: 20px;
		min-height: 34px;

		&:not(:last-child) {
			margin-bottom: 42px;
		}
		&:last-child {
			margin-bottom: 56px;
		}
		color: #828282;
		width: 70px;
		max-width: 89px;
	}
	&__card {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}

.rating {
	@media screen and (max-width: 940px) {
		display: flex;
	}
	li {
		background-repeat: no-repeat;
		background-position: center;
		display: inline-block;
		height: 26px;
		width: 26px;
		border-radius: 1px;
	}

	&__black-star {
		background-image: url(../images/Star-black.svg);
	}

	&__grey-star {
		background-image: url(../images/Star-grey.svg);
	}
}
